<template>
  <v-dialog v-model="dialog" width="75vw">
    <v-btn
      slot="activator"
      color="teal"
      dark
      fab
      fixed
      bottom
      right
      style="margin-bottom: 45px;"
    >
      <v-icon>add</v-icon>
    </v-btn>
    <v-card>
      <v-card-title class="headline teal lighten-3" primary-title>
        New Todo
      </v-card-title>

      <v-card-text>
        <v-text-field
          label="Todo Message"
          v-model="newTodoTxt"
          required
          color="teal"
          @input="checkMsg"
        ></v-text-field>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="dark-grey" flat @click="dialog = false"> Cancel </v-btn>
        <v-btn color="teal" flat :disabled="disabled" @click="addTodo">
          Add
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { vuexStore } from "../vuexStore.js";

export default {
  name: "AddTodo",
  data() {
    return {
      storeState: vuexStore.state,
      dialog: false,
      newTodoTxt: "",
      disabled: true
    };
  },
  methods: {
    addTodo() {
      this.dialog = false;

      vuexStore.commit("createTodo", {
        id: Math.random()
          .toString(36)
          .substr(2, 9),
        complete: false,
        txt: this.newTodoTxt
      });

      this.newTodoTxt = "";
      this.disabled = true;
    },
    checkMsg() {
      if (this.newTodoTxt === "") {
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    }
  }
};
</script>
