<template>
  <v-toolbar color="teal darken-4" app dark>
    <v-btn v-if="storeState.backBtnVisible" icon @click="goBack"
      ><v-icon>arrow_back</v-icon></v-btn
    >
    <v-toolbar-title class="headline text-uppercase">
      <span>TODO</span> <span class="font-weight-light">LIST</span>
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn icon @click="installer()" :style="{ display: installBtn }">
      <v-icon>system_update</v-icon>
    </v-btn>
  </v-toolbar>
</template>

<script>
import { vuexStore } from "../vuexStore.js";

export default {
  name: "Header",
  data() {
    return {
      storeState: vuexStore.state,
      installBtn: "none",
      installer: undefined
    };
  },
  methods: {
    goBack() {
      vuexStore.commit("setBackBtnVisibility", false);

      this.$router.go(-1);
    }
  },
  created() {
    let installPrompt;

    window.addEventListener("beforeinstallprompt", e => {
      e.preventDefault();
      installPrompt = e;
      this.installBtn = "block";
    });

    this.installer = () => {
      this.installBtn = "none";
      installPrompt.prompt();

      installPrompt.userChoice.then(result => {
        if (result.outcome === "accepted") {
          console.log("User accepted");
        } else {
          console.log("User denied");
        }
        installPrompt = null;
      });
    };
  }
};
</script>
