<template>
  <v-list>
    <v-list-tile v-for="todo in storeState.todos" :key="todo.id">
      <v-list-tile-avatar @click="toggleTodoStatus(todo)">
        <v-icon :color="todo.complete ? 'teal' : 'grey'">check_box</v-icon>
      </v-list-tile-avatar>

      <v-list-tile-content>
        <div v-if="todo.complete">
          <s><v-list-tile-title v-html="todo.txt"></v-list-tile-title></s>
        </div>
        <div v-else>
          <v-list-tile-title v-html="todo.txt"></v-list-tile-title>
        </div>
      </v-list-tile-content>

      <v-list-tile-action @click="showDetails(todo)">
        <v-icon color="teal">edit</v-icon>
      </v-list-tile-action>
    </v-list-tile>
  </v-list>
</template>

<script>
import { vuexStore } from "../vuexStore.js";

export default {
  name: "TodoList",
  data() {
    return {
      storeState: vuexStore.state
    };
  },
  methods: {
    showDetails(todo) {
      vuexStore.commit("setActiveTodo", todo);
      vuexStore.commit("setBackBtnVisibility", true);
      this.$router.push("about");
    },
    toggleTodoStatus(todo) {
      vuexStore.commit("toggleTodoStatus", todo);
    }
  }
};
</script>
