import Vue from "vue";
import Vuex from "vuex";
import VuexPersist from "vuex-persist";

Vue.use(Vuex);

const vuexPersist = new VuexPersist({
  key: "vue-pwa-todo",
  storage: localStorage
});

export const vuexStore = new Vuex.Store({
  plugins: [vuexPersist.plugin],
  state: {
    todos: [
      { id: "abcd1234", complete: true, txt: "Make Coffee" },
      { id: "1234abcd", complete: false, txt: "Eat Lunch" }
    ],
    activeTodo: null,
    backBtnVisible: false
  },
  mutations: {
    createTodo(state, newTodo) {
      state.todos.push(newTodo);
    },
    updateTodo(state, { id, txt }) {
      let todoID = state.todos.findIndex(obj => obj.id == id);
      state.todos[todoID].txt = txt;
    },
    deleteTodo(state, id) {
      state.todos = state.todos.filter(todo => todo.id !== id);
    },
    toggleTodoStatus(state, todo) {
      let todoID = this.state.todos.findIndex(obj => obj.id == todo.id);
      state.todos[todoID].complete = !state.todos[todoID].complete;
    },
    setActiveTodo(state, todo) {
      state.activeTodo = todo;
    },
    setBackBtnVisibility(state, status) {
      state.backBtnVisible = status;
    }
  }
});
